
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import {mavonEditor} from "mavon-editor";
import "mavon-editor/dist/css/index.css";
export default {
name:"edit",
props:['id'],
components:{
XButton,
XTextarea,
Group,
mavonEditor
},
data(){
return{
topics:{
title:"",
content:"",
toolbars: {
bold: true, // 
italic: true, // 
header: true, // 
underline: true, // 
strikethrough: true, // 
mark: true, // 
superscript: true, // 
subscript: true, // 
quote: true, // 
ol: true, // 
ul: true, // 
link: true, // 
imagelink: true, // 
code: true, // code
table: true, // 
fullscreen: true, // 
readmodel: true, // 
htmlcode: true, // html
help: true, // 
/* 1.3.5 */
undo: true, // 
redo: true, // 
trash: true, // 
save: true, // eventssave
/* 1.4.2 */
navigation: true, // 
/* 2.1.8 */
alignleft: true, // 
aligncenter: true, // 
alignright: true, // 
/* 2.2.1 */
subfield: true, // 
preview: true, // 
}
}
}
},
methods:{
editTopic(){
var app=this;
this.$http.post("/topics/update",{
accesstoken:this.$store.state.accesstoken,
topic_id:this.topic_id,
tab:"dev",
title:this.topics.title,
content:this.topics.content
}).then(function (res) {
console.log(res.data);
if (res.data.success){
app.$router.back();
}
})
}
},
beforeRouteEnter(to,from,next){
next(function (vm) {
vm.$http.get("/topic/"+to.params.id,{
params:{
accesstoken:vm.$store.state.accesstoken
}
}).then(function (res) {
if (res.data.success){
vm.topics=res.data.data;
}
})
})
},
beforeRouteUpdate(to,from,next){
var app=this;
this.$http.get("/topic/"+to.params.id,{
params:{
accesstoken:this.$store.state.accesstoken
}
}).then(function (res) {
if (res.data.success){
app.topics=res.data.data;
}
})
next();
}
}



